
/* CSS Boilerplate*/

*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body{
  background-color: black;
}


.container{
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;  
}

.calsi{
  padding: 20px;
  border-radius: 20px;
  border: 2px solid black;
  background-color: red;
  color: white;
  box-shadow: -1px -1px 10px 10px pink;
  text-align: center;
}

.input{
  width: 100%;
  max-width: 400px;
  border-radius: 20px;
  height: 100px;
  border: none; 
  color: white;  

}

button{
  width: 50px;
  height: 50px;
  margin: 5px;
  border-radius: 50%;
  border: none;
  /* color: red; */
  cursor: pointer;

}

p{
  justify-content: center;
}